@media screen and (max-width: 1200px){
  .catalog-item{
    &__inner{
      grid-template-columns: 400px calc(100% - 430px);
      gap: 30px;
    }
  }

  .gallery{
    &__small {
      .swiper {
        &-slide {
          width: calc(100% / 3);
        }
      }
    }
  }

  .catalog-info{
    padding: 14px;
    border-radius: 10px;
    .tab-content{
      padding: 14px;
    }
  }

  .services-slider{
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .qa-card{
    &__hidden{
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .services-page{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 32px / 3));
    }
  }

  .site-item{
    margin-right: 20px;
  }

  .portfolio{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 16px));
    }
  }

  .team{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 16px;
    }
  }

  .team-card{
    grid-template-columns: 150px calc(100% - 160px);
    gap: 10px;
  }

  .certificates{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 16px));
      gap: 24px;
      margin-top: 40px;
    }
  }

  .contacts{
    &__col{
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .map{
    margin-top: 40px;
  }

  .about-gallery{
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    gap: 20px;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .gallery{
    &__small{
      display: none;
    }
    &__big{
      .swiper{
        overflow: unset;
        &-slide{
          padding-right: 10px;
          width: 300px;
        }
      }
    }
  }

  .services-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .services-page__text, .text35{
    font-size: 28px;
  }

  .page-block{
    padding: 30px 0 60px;
  }

  .faq{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 25px;
    }
    &__desc{
      margin-top: 10px;
    }
  }

  .services-page{
    &__grid{
      margin-top: 25px;
      grid-template-columns: repeat(2,calc(50% - 8px));
    }
    &__text{
      margin-top: 40px;
    }
  }

  .consult{
    border-radius: 16px;
    margin-top: 40px;
    &__col1,
    &__col2{
      padding: 40px;
    }
  }

  .reviews{
    &__inner{
      padding: 30px 0 60px;
    }
    &__items{
      margin-top: 16px;
      width: calc(100% + 16px);
      max-width: unset;
    }
    &__img{
      display: none;
    }
  }

  .site-item{
    margin-right: 16px;
    width: calc(100% / 3 - 16px);
  }

  .portfolio{
    &__grid{
      margin-top: 25px;
      grid-template-columns: repeat(2,calc(50% - 6px));
      gap: 12px;
    }
  }

  .team{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .certificate-card{
    padding: 15px;
  }

  .contacts{
    &__grid{
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      margin-top: 30px;
    }
    &__col{
      padding: 0 !important;
      max-width: 100% !important;
      min-height: unset;
      &:not(:last-child){
        border-bottom: 1px solid #ccc;
        padding-bottom: 20px !important;
      }
      &:after{
        display: none;
      }
    }
  }

  .contacts-item{
    @include lastM(0 0 25px);
    &__desc{
      min-height: unset;
    }
  }

  .map{
    height: 350px;
  }

  .about-gallery{
    margin-top: 25px;
  }

  .about{
    &__text{
      margin-top: 30px;
    }
  }

  .review-card{
    grid-template-columns: 100%;
    gap: 20px;
    &__left{
      padding-right: 0;
    }
    &__right{
      padding: 20px 0 0;
      border-left: none;
      border-top: 1px solid #bebebe;
    }
  }
}


@media screen and (max-width: 768px){
  .services-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .catalog-info{
    border-radius: 0;
    padding: 10px;
    .tabs{
      width: calc(100% + 20px);
      margin-left: -10px;
      padding: 0 10px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    .tab{
      flex-shrink: 0;
    }
  }

  .consult{
    grid-template-columns: 100%;
    &__desc,
    &__check{
      max-width: 100%;
    }
  }

  .site-item{
    width: calc(50% - 16px);
  }

  .certificates{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 12px));
    }
  }

  .order-form{
    padding: 20px 15px 0;
    &__field{
      grid-template-columns: 100%;
      .order-form__title,
      label{
        padding: 0;
        text-align: left;
      }
    }
    &__count{
      display: block;
      &-text{
        margin: 8px 0 0;
      }
    }
  }

  .review-card{
    padding: 20px;
  }
}


@media screen and (max-width: 576px){
  .m-text h3,
  .text35{
    font-size: 28px;
  }

  .gallery{
    &__big{
      .swiper{
        &-slide{
          max-width: 250px;
          width: 80%;
        }
      }
    }
  }

  .services{
    &__title{
      font-size: 28px;
    }
  }

  .services-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .consult{
    grid-template-columns: 100%;
    &__col1,
    &__col2{
      padding: 30px 20px;
    }
  }

  .consult-form{
    &__fields{
      margin-top: 25px;
      gap: 20px;
    }
    &__btn{
      margin-top: 25px;
    }
  }

  .site-item{
    width: calc(100% - 16px);
  }

  .portfolio-modal{
    &__desc{
      padding: 8px 15px;
    }
  }

  .portfolio{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .team-card{
    align-items: flex-start;
    grid-template-columns: 120px calc(100% - 130px);
    &__name{
      font-size: 20px;
    }
    &__prof{
      font-size: 14px;
    }
    &__desc{
      font-size: 14px;
    }
  }

  .certificates{
    &__grid{
      margin-top: 20px;
      grid-template-columns: repeat(2,calc(50% - 6px));
      gap: 12px;
    }
  }

  .certificate-card{
    padding: 5px;
  }

  .about-gallery{
    grid-template-columns: 100%;
    gap: 12px;
  }

  .gallery-item {
    &._big {
      grid-column: unset;
      grid-row: unset;
    }
  }

  .file-input{
    padding: 8px;
    flex-direction: column;
    &__hint{
      margin: 8px 0 0;
    }
  }

  .call-form{
    padding: 20px 15px 0;
  }

  .review-card{
    &__top{
      display: block;
    }
    .rating-block{
      margin: 10px 0 0;
    }
  }
}
